  computed: {
    /*
    filteredKuyular() {
      return this.kuyular.filter(
        (kuyu) =>
          kuyu.kaydeden_kullanici == this.user.email.toUpperCase().slice(0, -10)
      );
    }, 
 */

    filteredList() {
      return this.kuyular.filter((kuyu) => {
      return kuyu.kuyu_adi.toLowerCase().match(this.search.toLowerCase())
      || kuyu.kuyu_sahibi.toLowerCase().match(this.search.toLowerCase())
       || kuyu.hizmet_no.toString().match(this.search.toString());
      
      
     });   
    },

